body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding:0;
  margin:0;
}
body{
  background-color:#111;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.player{
  margin-top:1rem;
  margin-bottom:1rem;
}
.material-icons{
  background-color:transparent;
  color: #fff;
  border-radius:10px;
  height:30px;
  width: 30px;
  border-style: hidden;
}
.navbar{
    position: fixed;
    top:0;
    width:100%;
    height:10px;
    display:flex;
    padding:20px;
    justify-content: center;
    background-color:transparent;
}
.logo{
    position: fixed;
    width:80px;
    left:20px;
}
.avatar{
    position: fixed;
    right:20px;
    width:25px;
}
.search{
    width:300px;
    height:30px;
    border-radius:10px;
    border-style: none;
}
.searchList{
    background-color: #111;
    color:#fff;
}
.searchitem{
    background-color: #111;
    color:#fff;
    text-align: center;
    margin-bottom:2px;
}
.searchbar{
    display:flex;
    flex-direction: column;
}

.banner{
    background-size: cover;
    height: 435px;
    width:100%;
    background-position:0% 10%;
}
.logo_n{
    height:30px;
    width:30px;
}
.banner_content{
    padding-top: 80px;
    padding-left:15px;
    max-height:200px;
    max-width:400px;
    color: #fff;
}
.nseries{
 display: flex;
}
.nseries_series{
    margin-top:0.5rem;
}
.banner_button button{
    border-style: hidden;
    border-radius:10%;
    height: 30px;
    width:100px;
    margin-right:1rem;
    outline:none;
    background-color: rgb(94, 92, 92,0.5);
    color: #fff;
}
.gradient{
    height: 7rem;
    margin-top:3.5rem;
     background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
}
.banner_discription{
    margin-top:2rem;
    line-height:1.6;
    width:450px;
    max-height:100px;
    overflow-y: scroll;
    overflow-x: hidden;
}
.banner_discription p{
    color:rgb(255, 255, 255,0.7);
}
.title{
    font-size:30px;
    line-height:0.8;
}
.banner_button{
    margin-top:1rem;
}
.banner_discription::-webkit-scrollbar{
    display: none;
}
.rowposter{
    color: #fff;
    margin-top:-1.5rem;
    margin-bottom:3rem;
}

.container{
    margin-top:1rem;
    display:flex;
    overflow-x: scroll;
    overflow-y: hidden;
}
.poster{
    height: 200px;
    width:300px;
    margin-right:-0.5rem;
    background-size:cover;
    margin-left:1rem;
}
.container::-webkit-scrollbar{
    display: none;
}
.typehead{
    margin-left:15px;
}

.poster:hover{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
